.page-header {
  text-transform: uppercase;
  font-size: 18px;
  line-height: 26px;
  position: relative;
  background: #ffffff;
}
@media (max-width: 767px) {
  .page-header {
    min-height: 56px;
  }
}

.main-nav__site-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
@media (min-width: 768px) {
  .main-nav__site-list {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0;
    padding: 0;
  }
}

.main-nav__site-link {
  text-decoration: none;
  color: #ffffff;
  font-weight: 700;
  display: block;
}
@media (min-width: 768px) {
  .main-nav__site-link {
    font-size: 14px;
    line-height: 26px;
    color: #333333;
    font-weight: 700;
  }
  .main-nav__site-link:hover {
    color: #81b3d2;
  }
  .main-nav__site-link:active, .main-nav__site-link:focus {
    color: #dbdbdb;
  }
}

.main-nav {
  position: relative;
}

.main-nav__wrapper {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    background-color: #ffffff;
  }
}

.main-nav--nojs .main-nav__toggle {
  display: none;
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__wrapper::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    width: 7px;
    height: 100%;
    box-shadow: inset 4px 0 7px -2px fade(#000000, 40%);
  }

  .main-nav--closed .main-nav__site-list {
    display: none;
  }
}
.main-nav--opened .main-nav__toggle {
  position: absolute;
  border: none;
  background-image: url("../img/header/icon-menu-open.svg");
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  height: 23px;
  width: 22px;
  top: 17px;
  right: 20px;
  background-color: transparent;
  z-index: 5;
}

.main-nav--closed .main-nav__toggle {
  position: absolute;
  border: none;
  background-image: url("../img/header/icon-menu.svg");
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  height: 23px;
  width: 22px;
  top: 17px;
  right: 20px;
  background-color: transparent;
  z-index: 5;
}
@media (min-width: 768px) {
  .main-nav--closed .main-nav__toggle {
    display: none;
  }
}

.main-nav__site-item {
  background-color: #81b3d2;
  text-align: center;
  border-top: 1px solid #9ac2db;
  border-bottom: 1px solid #678fa8;
  padding: 12px 0 17px 0;
}
@media (max-width: 767px) {
  .main-nav__site-item:hover {
    background-color: #669ec0;
  }
